.mark {
  display: flex;
  position: relative;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  cursor: pointer;
  border: 1px solid #48455C;
}
.mark::before {
  content: "";
  position: absolute;
  opacity: 0;
}
.mark--radio {
  border-radius: 50%;
}
.mark--radio::before {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #48455C;
}
.mark--checkbox {
  border-radius: 6px;
}
.mark--checkbox::before {
  top: -1px;
  left: -1px;
  bottom: -1px;
  right: -1px;
  border-radius: 6px;
  background: center/50% url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e") no-repeat, #3075FC;
}
.mark--blue {
  border-color: #3075FC;
}
.mark--blue::before {
  background-color: #3075FC;
}
.mark--white {
  border-color: #FFFFFF;
}
.mark--white::before {
  background-color: #FFFFFF;
}

.input-with-mark:checked + .mark::before {
  opacity: 1;
}

